:root {
  --border-radius: 15px;

  --box-shadow-inset-diagonal-dark: inset -6px -6px 16px rgba(68, 68, 102, 0.5), inset 6px 6px 16px rgba(0, 0, 17, 0.5);
  --box-shadow-inset-diagonal-light: inset 6px 6px 16px rgba(187, 187, 187, 0.5), inset -6px -6px 16px rgba(255, 255, 255, 0.5);

  --box-shadow-inset-vertical-dark: inset 0 -6px 16px rgba(68, 68, 102, 0.5), inset 0 6px 16px rgba(0, 0, 17, 0.5);
  --box-shadow-inset-vertical-light: inset 0 6px 16px rgba(187, 187, 187, 0.5), inset 0 -6px 16px rgba(255, 255, 255, 0.5);

  --box-shadow-outset-dark: -6px -6px 16px rgba(68, 68, 102, 0.5), 6px 6px 16px rgba(0, 0, 17, 0.5);
  --box-shadow-outset-light: 6px 6px 16px rgba(187, 187, 187, 0.5), -6px -6px 16px rgba(255, 255, 255, 0.5);

  --color-dark: #333344;
  --color-light: #dddde2;

  --font-family: 'Roboto';

  --text-shadow-dark: -6px -6px 16px rgba(68, 68, 102, 0.5), 6px 6px 16px rgba(0, 0, 17, 0.5);
  --text-shadow-light: 6px 6px 16px rgba(187, 187, 187, 0.5), -6px -6px 16px rgba(255, 255, 255, 0.5);
}

* {
  font-family: var(--font-family);
  font-style: normal;
}

.App-dark {
  background: var(--color-dark);
}

.App-dark * {
  color: var(--color-light);
  text-shadow: var(--text-shadow-dark);
}

.App-light {
  background: var(--color-light);
}

.App-light * {
  color: var(--color-dark);
  text-shadow: var(--text-shadow-light);
}
