.banner {
  margin: 8px 0;
}

.color-list {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.color-container {
  margin: 8px 0;
  padding: 8px;
  width: calc(50% - 8px);
  border: 1px solid #000;
  position: relative;
  box-sizing: border-box;
}

.color {
  border-radius: 50%;
  height: 100px;
  width: 100px;
  background: #000;
  margin: 0 auto 8px;
}

.color-text {
  text-align: center;
  margin: 8px 0 0;
}

